@import url(https://fonts.googleapis.com/css?family=Handlee&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.header {
  display: flex;
  width: 100%;
  height: 10vh;
  padding: 10px 5px;
  align-items: center;
  justify-content: space-between;
  background-color: #2e3032;
  border-bottom: 1px solid grey;
}

.chat-status {
  position: absolute;
  left: -30px;
  top: 5px;
  width: 15px;
  height: 15px;
  content: '';
  border-radius: 50%;
  cursor: default;
}

.chat-status_online {
  background-color: lightgreen;
}

.chat-status_offline {
  background-color: red;
}

.header__welcome {
  padding: 10px 30px;
  color: rgb(245, 237, 237);
  font-family: 'Handlee', cursive;  
}

.welcome__name {
  color: burlywood; 
}

.header__logout {
  position: relative;
  display: flex;
  margin-right: 15px;
  margin-left: auto;
  color: rgb(245, 237, 237);
  cursor: pointer;
}

.logout__img {
  width: 24px;
  height: 24px;
  margin-left: 5px;
}

.welcome__login_mobile {
  font-size: 0;
}

@media (max-width: 1000px), (max-height: 400px) {
  .header__welcome {
    font-size: 0;
  }

  .welcome__name {
    font-size: 18px;
  }

  .welcome__login_mobile {
    font-family: 'Handlee', cursive; 
    font-size: 24px;
  }
}

@media (max-width: 400px) {
  .logout__img {
    display: none;
  }

  .header__welcome {
    padding: 5px;
  }

  .header__logout {
    margin: 0 5px;
  }
}

@media (max-width: 330px) {
  .welcome__name {
    display: none;
  }
}

@media (max-height: 300px) {
  .welcome__login_mobile {
    font-size: 16px;
  }

  .logout__img {
    display: none;
  }

  .welcome__name {
    font-size: 16px;
  }
}
.login {
  display: flex;
  height: 100vh;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #36393e;
}

.login__form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.login__user {
  width: 300px;
  height: 50px;
  padding: 0 10px;
  margin: 100px auto 10px auto;
  font-size: 18px;
  border: 1px solid grey;
  border-radius: 5px;
}

.login__user:focus {
  outline: none;
}

.login__enter {
  width: 170px;
  margin: 0 auto;
  padding: 10px 5px;
  text-align: center;
  font-size: 18px;
  background-color: burlywood;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.login__enter:focus {
  outline: none;
}

.login__enter:hover {
  background-color: rgb(226, 178, 114); 
}

@media (max-width: 400px) {
  .login__user {
    width: 90%;
  }

  .login__enter {
    width: 60%;
  }
}

@media (max-height: 250px) {
  .login__user {
    margin-top: 20px;
  }  
}
.messages-container {
  display: flex;
  width: 100%;
  height: 75vh;
  margin: 0 auto;
  padding: 0 30px;
  align-content: flex-start;
  flex-wrap: wrap;
  overflow-Y: scroll;
  background-color: #36393e;
}

.messages-container__load-more {
  margin: 10px auto 0 auto;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 17px;
  background-color: burlywood;
  cursor: pointer;  
}

.load-more_disabled {
  display: none;
}

.load-more_active {
  display: block;
}

.messages-container__load-more:focus {
  outline: none;
}

.messages-container__load-more:hover {
  background-color: rgb(226, 178, 114); 
}

.message {
  width: 100%;
  padding: 0 0 9px 2px;
  margin: 5px 0;
  border-bottom: 1px solid grey;
  overflow: hidden;
}

.message__author {
  margin-right: 5px;
  font-size: 17px;
  color: burlywood;
}

.message__date {
  color: rgb(172, 170, 170);
  font-size: 13px;
}

.message__text {
  margin-top: 7px;
  font-size: 16px;
  color: #fff;
}

@media (max-width: 800px) {
  .messages-container {
    padding: 0 10px;
  }
}

@media (max-height: 600px) {
  .message {
    padding: 0 0 9px 2px;
  }
}

.user-message {
  display: flex;
  width: 100%;
  height: 15vh;  
  margin: 0 auto;
  padding: 20px 30px;
  justify-content: center;
  align-items: center;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: #36393e;
}

.user-message__textarea {
  width: 100%;
  padding: 10px;
  margin-right: 20px;
  border-radius: 10px;
  font-size: 18px;
  resize: none;
}

.user-message__textarea:focus,
.user-message__send:focus {
  outline: none;
}

.user-message__send {
  width: 40px;
  height: 40px; 
  color: #fff;
  font-size: 0;
  border:none;
  box-shadow: none;
  background-color:transparent;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  cursor: pointer;
}

@media (max-width: 800px) {
  .user-message__send {
    width: 25px;
    height: 25px;
    background-size: cover;
  }

  .user-message__textarea {
    margin-right: 5px;
  }

  .user-message {    
    justify-content: space-between;
    padding: 10px;
  }
}

@media (max-width: 400px) {
  .user-message__textarea {
    width: 85%;
  }
}

@media (max-height: 500px) {
  .user-message__textarea {
    height: 48px;
    margin-right: 5px;
  }

  .user-message__send {
    width: 25px;
    height: 25px;
    background-size: cover;
  }
}

@media (max-height: 350px) {
  .user-message__textarea {
    height: 35px;
    padding: 5px 10px;
    font-size: 14px;
  }
  
  .user-message {
    padding: 0 30px 10px 30px;
  }  
}

.chat-room {
  height: 90vh;
  background-color: #2f3136;
}

.chat {
  position: relative;
  width: 85%;
  height: 90vh;
  margin: 0 auto;
  background-color: #36393e;
}

::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 1px grey; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: burlywood; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(220, 168, 100); 
}




* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}


